<style>
  .ivu-select-dropdown {
    overflow-x: hidden!important;
  }
  .default .ivu-select-item {
    padding: 5px 10px;
    color: #ddd;
  }
  .area-col {
    width: 32%;
  }
  .area-right {
    margin-left: 2%;
  }
</style>
<template>
    <Row>
      <iCol span="10">
        <Form-item  label="地址：" prop="addrProv">
          <iSelect class="grey" clearable v-model="f.p" :disabled="isDisabled" placeholder="请选择省份" @on-change="selpro">
            <Option :value="i" :key="i" v-for="(v,i) in pro">{{v.name}}</Option>
          </iSelect>
        </Form-item>
      </iCol>
      <iCol span="7">
        <Form-item prop="addrCity" :label-width="10">
          <iSelect class="grey" clearable v-model="f.c" :disabled="isDisabled" placeholder="请选择城市" @on-change="selcity">
            <Option :value="i" :key="i" v-for="(v,i) in city">{{v.name}}</Option>
          </iSelect>
        </Form-item>
      </iCol>
      <iCol span="7">
        <Form-item prop="addrDistrict" :label-width="10">
          <iSelect class="grey" clearable v-model="f.cc" :disabled="isDisabled" placeholder="请选择县区" @on-change="result">
            <Option :value="i" :key="i" v-for="(v,i) in county">{{v.name}}</Option>
          </iSelect>
        </Form-item>
      </iCol>
    </Row>

</template>

<script>
  import data from "./data";
  import lodash from 'lodash';
  import Vue from 'vue';
  import niceScroll from 'jquery.nicescroll';

  export default {
    props: [
      'address', 'disabled'
    ],
    data:function(){
      return {
        data: data,
        isDisabled: false,
        pro: "",
        city: "",
        county: "",
        isInit: false,
        f: {
          p: 0,
          c: 0,
          cc: 0,
        },
      }
    },
    created:function(){
      this.initData();
    },
    methods:{
      selpro:function(){
        if(this.isInit) {
          return;
        }
        if(this.f.p === '') {
          this.city = [];
          this.county = [];
          this.f.c='';
          this.f.cc='';
          this.result();
          return;
        }
        this.city=this.pro[this.f.p]['child'];
        this.county=this.city[0]['child'];
        this.f.c='';
        this.f.cc='';
        this.result();
      },
      selcity:function(){
        if(this.isInit) {
          return;
        }
        if(this.f.c === '') {
          this.county = [];
          this.f.cc='';
          this.result();
          return;
        }
        this.county=this.city[this.f.c]['child']?this.city[this.f.c]['child']:[];
        this.f.cc='';
        this.result();
      },
      result:function(){
        let address={};
        if(this.f.p === '') {
          this.$emit("select",address);
          return;
        }
        address.addrProv = this.pro[this.f.p].name;
        if(this.f.c === '') {
          this.$emit("select",address);
          return;
        }
        address.addrCity = this.city[this.f.c].name;
        if(this.county.length>0 && this.f.cc !== ''){
          address.addrDistrict = this.county[this.f.cc].name;
        }else{
          address.addrDistrict = undefined;
        }
        this.$emit("select",address);
      },
      initData: function() {
        this.isInit = true;
        this.pro=this.data;
        if(this.address !== undefined) {
          // 已经输入了省市区
          if(this.address.addrProv === undefined || this.address.addrProv === null) {
            this.f.p = this.f.c = this.f.cc = '';
            this.city=[];
            this.county=[];
            Vue.nextTick(() => {
              this.isInit = false;
            });
            return;
          }
          this.f.p = _.findIndex(this.pro, {name: this.address.addrProv});
          this.city=this.pro[this.f.p]['child'];
          if(this.address.addrCity === undefined || this.address.addrCity === null) {
            this.f.c = this.f.cc = '';
            this.city=[];
            this.county=[];
            Vue.nextTick(() => {
              this.isInit = false;
            });
            return;
          }
          this.f.c = _.findIndex(this.city, {name: this.address.addrCity });
          if(this.address.addrDistrict === undefined || this.address.addrDistrict === null) {
            this.f.cc = '';
            this.county=[];
            Vue.nextTick(() => {
              this.isInit = false;
            });
            return;
          }
          this.county=this.city[this.f.c]['child'];
          this.f.cc = _.findIndex(this.county, {name: this.address.addrDistrict });
        } else {
          this.city=this.pro[0]['child'];
          this.county=this.city[0]['child'];
        }
        this.result();
        Vue.nextTick(() => {
          this.isInit = false;
        });
      }
    },
    mounted() {
      this.isDisabled = this.disabled === undefined ? false : this.disabled;
      $(".ivu-select-dropdown").niceScroll({
        cursorcolor: "#6a727e",
        cursorwidth: "5px",
        cursoropacitymax: 0.4,
        cursorborder: "", // css definition for cursor border
        cursorborderradius: "",
        hidecursordelay: 100,
        horizrailenabled: false,
      });
    }
  }
</script>



