<style>
  .ivu-form .ivu-form-item-label{
       color: #ddd;
       font-size: 14px;
     }
  .ivu-form-label-top .ivu-form-item-label {
    padding: 0px;
  }
  .info .ivu-form-item-label {
    padding: 10px 5px 10px 0;
  }
</style>
<template>
  <!--右侧内容总div-->
  <div class="right-container">
    <Row class="container-navigation">
      <iCol span="2">
        <img src="../../../assets/content/navigate_icon.png" height="20" width="20"/>
      </iCol>
      <iCol span="22">
      <span class="page-title">基本信息</span>
      </iCol>
    </Row>
    <div class="back-to-list" v-if="canReturn" @click="returnPage"></div>
    <!--<div class="back-to-list" v-if="from === 'system'" @click="returnSystem"></div>-->
    <!--<div class="back-to-list" v-else-if="from === 'supervision'" @click="returnSupervision"></div>-->
    <Row class="right-content">
      <iCol span="15">
        <Form ref="baseInfoFrom":model="baseInfo" :label-width="110" class="default" style="padding: 10px;" :rules="baseInfoValidate">
          <Form-item prop="name" label="名称：" style="padding:0px;">
            <Input :disabled="!editable" type="text" class="grey" placeholder="输入名称" v-model="baseInfo.name"/>
          </Form-item>
          <myarea :disabled="!editable" ref="myarea" @select="myareaOut" :address="address" class="default"></myarea>
          <Form-item prop="addrStreet"  label="详细地址：">
            <Input :disabled="!editable"  v-model="baseInfo.addrStreet" placeholder="输入详细地址"  class="grey" />
          </Form-item>
          <Row>
            <iCol span="10">
              <Form-item prop="contactNum" label="单位电话：":label-width="110">
                <Input :disabled="!editable" type="text" class="grey" placeholder="输入单位电话"  v-model="baseInfo.contactNum"/>
              </Form-item>
            </iCol>
            <iCol span="7">
              <Form-item class="info" prop="ownerName" label="负责人：":label-width="75">
                <Input :disabled="!editable" type="text" class="grey" placeholder="输入负责人" v-model="baseInfo.ownerName"/>
              </Form-item>
            </iCol>
            <iCol span="7">
              <Form-item class="info" prop="ownerTel" label="电话：" :label-width="60">
                <Input :disabled="!editable" type="text" class="grey" placeholder="输入电话"  v-model="baseInfo.ownerTel"/>
              </Form-item>
            </iCol>
          </Row>
          <Row :gutter="8">
            <iCol span="12">
              <Form-item prop="supervLevel" label="消防监管等级：">
                <Select :disabled="!editable" v-model="baseInfo.supervLevel" placeholder="选择消防监管等级" size="small" class="grey">
                  <Option v-for="supervision in supervisionList" :value=supervision.codeId>{{supervision.codeValue}}</Option>
                </Select>
              </Form-item>
            </iCol>
            <iCol span="12">
              <Form-item prop="businessType" label="业态：" >
                <Select :disabled="!editable" :title="isFireUnitType ? fireUnitType.codeValue : ''" v-model="baseInfo.businessType" placeholder="选择业态" size="small" width="400px" class="grey" @on-change="businessChange">
                  <Option v-for="fireUnitType in fireUnitTypeList" :title="fireUnitType.codeValue" :value=fireUnitType.codeId style="width:160px;overflow: hidden;white-space: nowrap;text-overflow: ellipsis;">{{fireUnitType.codeValue}}</Option>
                </Select>
              </Form-item>
            </iCol>
          </Row>
          <!--<Form-item prop="smallPlaces" label="九小场所：" >-->
            <!--<Checkbox :disabled="!editable" size="large" v-model="baseInfo.smallPlaces"></Checkbox>-->
          <!--</Form-item>-->
          <Row>
            <iCol span="3" offset="17" >
              <FormItem style="margin-left: 19px; margin-top:-10px;">
                <Button type="primary" v-if="editable==true" class="grey" @click="saveBaseInfo('baseInfoFrom')" style="margin-top: 20px;">保存</Button>
              </FormItem>
            </iCol>
          </Row>
        </Form>
      </iCol>
      <iCol span="9">
        <div id="qr-image" style="margin-top: 14px; margin-left: 10px">
        </div>
      </iCol>
    </Row>
  </div>
</template>
<script>
  import niceScroll from 'jquery.nicescroll';
  import '../../../libs/jquery.citys.js';
  import myArea from '../../../components/vue-myarea/area.vue';
  import FormItem from "../../../../node_modules/iview/src/components/form/form-item.vue";
  import Checkbox from "../../../../node_modules/iview/src/components/checkbox/checkbox.vue";
  import server from "../../../config/api";

  export default {
    data(){
      return {
        isFireUnitType:true,
        canReturn: this.getYunSessionLength('route_list') > 1,
        fireUnitType:{},
        supervisionList:[],
        fireUnitTypeList:[],
        organizationId:'',
        baseInfo:{
          orgId:'',
          name: '',
          addrStreet: '',
          contactNum: '',
          ownerName: '',
          ownerTel: '',
          businessType: '',
          supervLevel: '',
          smallPlaces:false
        },
        orgClass:'NORMAL_ENTITY',
        address: {},
        baseInfoValidate: {
          name: [
            { required: true, message: '名称不能为空', trigger: 'blur' }
          ],
          addrProv: [
            {required: true, message: '请选择省份', trigger: 'blur'}
          ],
          addrCity: [
            {required: true, message: '请选择城市', trigger: 'blur'}
          ],
          addrDistrict: [
            {required: true, message: '请选择县区', trigger: 'blur'}
          ],
          addrStreet: [
            {required: true, message: '详细地址不能为空', trigger: 'blur'}
          ],
        }
      };
    },
    components: {
      Checkbox,
      FormItem,
      'myarea': myArea,
    },
    computed:{
      editable: function() {
        return this.hasPermission(this.$YDAuthCode.NormalEntity.OrganizationMgmt);
      }
    },
    methods: {
      init:function () {
        let image = new Image();
        image.onload=function () {
          image.width=107;
          $('#qr-image').html(image);
        };
        image.src=server.api.organization.building.getQr.replace('{type}','org').replace('{id}',this.organizationId)+"?width="+107+"&height="+107;
        this.$http.get(server.api.organization.baseInfo.get.replace('{orgId}', this.organizationId) + "?orgClass="+this.orgClass).then((response)=>{
          this.baseInfo = response.body.data;
          this.address.addrProv=this.baseInfo.addrProv;
          this.address.addrCity=this.baseInfo.addrCity;
          this.address.addrDistrict=this.baseInfo.addrDistrict;
          this.$refs.myarea.initData();
          if(this.baseInfo.businessType===null || this.baseInfo.businessType===undefined){
            this.isFireUnitType=false;
          } else {
            var fireUnit =[];
            fireUnit =_.filter(this.fireUnitTypeList, {'codeId': this.baseInfo.businessType});
            if(fireUnit.length === 0){
              this.isFireUnitType=false;
            }
          }
        },(error)=>{
          this.$Message.error('单位管理基本信息显示失败!');
        });
      },
      returnPage: function() {
        // 根据sessionStorage返回上一页
        this.popYunSession('route_list');
        let page = this.topYunSession('route_list');
        this.$router.push(page.router);
      },
      myareaOut: function(d) {
        this.address = d;
        console.log(this.address);
      },
      businessChange:function (businessType) {
        this.fireUnitType=_.filter(this.fireUnitTypeList, {'codeId': businessType})[0];
      },
      saveBaseInfo:function (name) {
        this.baseInfo.addrProv=this.address.addrProv;
        this.baseInfo.addrCity=this.address.addrCity;
        this.baseInfo.addrDistrict=this.address.addrDistrict;
        this.$refs[name].validate((valid) => {
          if (valid) {
            this.$http.put(server.api.organization.baseInfo.update,this.baseInfo).then((response)=>{
              this.$Message.success('修改成功！');
            },(error) => {
              this.$Message.error('修改失败！');
            })
          } else {
            this.$Message.error('表单验证失败!');
          }
        })
      },
      loadSystemCode: function () {
        let categories=[];
        categories.push('SUPERVISION_LEVEL');
        categories.push('FIRE_UNIT_TYPE');
        this.$http.get(server.api.organization.sysCode.getSysCode+'?categories[]='+categories).then((response)=>{
          this.systemCodeList=response.body.data;
          this.supervisionList=_.filter(this.systemCodeList, {'codeCategory': 'SUPERVISION_LEVEL'});
          this.fireUnitTypeList=_.filter(this.systemCodeList, {'codeCategory': 'FIRE_UNIT_TYPE'});
          this.init();
        },(error)=>{
          this.$Message.error("系统数据获取失败");
        })
      },
    },
    mounted(){
      debugger;
      let page = this.topYunSession('route_list');
      if(page === null || page.orgId === undefined) {
        // 根节点
        this.organizationId = localStorage.current_org_id;
      } else {
        this.organizationId = page.orgId;
      }
      this.loadSystemCode();
    }
  }
</script>



